@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Raleway:wght@200;900&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:200vh;width:100vw;background-image:url(/react-portfolio-ts/static/media/sky.cbd035fe.png);display:flex;flex-direction:column}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
*{--title-font: "Monoton", cursive;--body-font: "Raleway", sans-serif;--light-grey: #CCD3D4;--medium-grey: #AFADB2;--orange: rgb(255, 157, 0);--yellow: #FED93F;--red: #7E3B3A;--border: 2px dotted var(--light-grey)}ul{padding:0px}h2,h3,h4,p,li{font-family:var(--body-font);font-weight:200;color:var(--medium-grey);text-align:center}h3{font-weight:500px;font-size:30px}main.sections{background-color:#000;width:100%;margin:0px;z-index:10;display:flex;flex-direction:column;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory;scrollbar-width:none;scroll-behavior:smooth}main.sections::-webkit-scrollbar{display:none}main.sections>*{min-height:100vh;z-index:inherit}span.line-break{height:1px;width:80%;background-color:var(--light-grey)}@media screen and (min-width: 800px){h2,h3,h4,p,span,li,a{font-size:1.5rem}}
div.intro-card{display:flex;flex-direction:column;align-items:center;padding-top:5vh}div.mountain-background{width:100vw;height:100vw;margin:0px;background-size:100vw auto;background-image:url(/react-portfolio-ts/static/media/mountains.8bfa9dbb.png);background-repeat:no-repeat;z-index:10}h1{position:fixed;z-index:0}h1.intro{font-family:var(--title-font);font-weight:200;font-size:25px;width:60vw;left:5vw}h1.tagline{font-family:var(--body-font);font-size:10px;text-align:center;top:15vh;right:5vw;width:40vw}@media screen and (max-width: 1000px){h1.intro{width:100vw}h1.tagline{display:none}}@media screen and (min-width: 500px){h1.intro{font-size:2rem}h1.tagline{font-size:1.5rem}}@media screen and (min-width: 800px){h1.intro{font-size:5rem}h1.tagline{font-size:3rem}}
header.Header{margin:0px;width:100%;height:50px;z-index:21;background-color:#000;display:flex;justify-content:space-around;align-items:center;position:absolute;top:100vw}h2{font-size:20px}div.hamburger{width:32px;height:25px;display:flex;flex-direction:column;align-items:space-between;justify-content:space-between;cursor:pointer}div.hamburger span{width:100%;height:2px;background-color:var(--medium-grey);border-radius:2px;transform-origin:left;transition:all .5s ease}div.hamburger.active span:first-child{background-color:var(--yellow);transform:rotate(45deg)}div.hamburger.active span:nth-child(2){display:none}div.hamburger.active span:last-child{background-color:var(--yellow);transform:rotate(-45deg)}
nav.Nav{background-color:#000;padding-left:10px;padding-right:10px;z-index:20;transition:all 1s ease;position:absolute;top:calc(100vw + 50px);width:100%;padding:0px}nav.Nav.hidden{background-color:#000;padding-left:10px;padding-right:10px;top:100vw}ul.nav-list{display:flex;flex-direction:row;justify-content:space-evenly;margin:0px;padding:0px}li{list-style-type:none}li a{text-decoration:none;color:var(--medium-grey)}
div.about-me{display:flex;flex-direction:column;align-items:center;justify-content:space-between}div.headshot{background-image:url(/react-portfolio-ts/static/media/headshot.e99868be.png);background-size:90%;background-repeat:no-repeat;background-position:center;height:50vw;width:50vw}img{src:"../../images/headshot.png";height:100px;width:auto}p.about-body{margin:15px;padding:15px;border-radius:20px;border:var(--border)}@media screen and (min-width: 800px){div.headshot{height:350px;width:350px}}
.circular{border-radius:50%}h3{margin-top:100px;margin-bottom:100px;width:100%}div.projectContainer{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}div.projectItem{margin-top:20px;width:300px;height:300px;background-size:300px 300px;display:flex;flex-direction:column;align-items:center;justify-content:space-around;margin:10px}div.projectItem h4{display:inline;position:relative;font-weight:900;font-size:20px;color:#000;z-index:11}img{z-index:10;margin:none;bottom:0px;width:100%;height:100%;border-radius:30%}div.text{position:relative;display:flex;flex-direction:row;justify-content:space-evenly;top:-55%;z-index:11;background-color:rgba(255,255,255,.589);width:100%}div.links{display:flex;align-items:center}.icon{height:50px;color:#000}img:hover{opacity:70%}
div.resume{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}a.download{font-size:40px;font-family:var(--body-font);text-decoration:none;color:var(--light-grey)}a.download:hover{color:var(--red)}div.graph{display:flex;flex-direction:column;align-items:flex-start}span.bar{display:flex;justify-content:space-between;align-items:center;color:#000;font-family:var(--body-font);padding:10px;margin-top:10px;margin-bottom:10px;height:50px;background-color:var(--orange)}h3{margin-top:10px;margin-bottom:10px}span.css{width:60vw}span.js{width:50vw}span.react{width:30vw}p.proficiency{color:#535353}.scale-in-hor-left{animation:scale-in-hor-left 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}@keyframes scale-in-hor-left{0%{transform:scaleX(0);transform-origin:0% 0%;opacity:1}100%{transform:scaleX(1);transform-origin:0% 0%;opacity:1}}
div.contact{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}h2{font-weight:500}div.their-info{display:flex;flex-direction:column;align-items:center}form{padding:10px;width:80vw;height:400px;display:flex;flex-direction:column;align-items:center;border-radius:20px;border:var(--border);margin-bottom:100px}form input{font-family:var(--body-font);font-size:20px;opacity:.8;margin:5px;width:80%;height:15%;border:none;text-align:center}input.name{border-radius:10px 10px 0px 0px;margin-bottom:2px}input.body{border-radius:0px 0px 10px 10px;margin-top:2px;height:70%}form button.button{height:25px;background-color:var(--light-grey);border:none;border-radius:20px;font-family:var(--body-font)}
footer.Footer{display:flex;flex-direction:row;justify-content:space-around;align-items:center;background:linear-gradient(black 0%, #AFADB2 30%, #CCD3D4 70% 100%);bottom:0px;height:80px}footer a{color:#000;font-family:var(--body-font);font-weight:200;text-decoration:none;font-size:30px;padding:none;margin-top:20px}footer a:hover{color:var(--red);font-weight:500}
